___CSS_LOADER_EXPORT___.push([module.id, ".comment-form[data-v-b74c7cc6]{min-width:350px;width:90%}.profile[data-v-b74c7cc6]{border-radius:50%;width:50px}.input-group-text[data-v-b74c7cc6]{color:#e73862;font-size:1.3rem}", "",{"version":3,"sources":["webpack://./resources/js/components/Products/Detail.vue"],"names":[],"mappings":"AAuQA,+BACI,eAAgB,CAChB,SACJ,CACA,0BAEI,iBAAkB,CADlB,UAEJ,CAEA,mCACI,aAAc,CACd,gBACJ","sourcesContent":["<template>\n    <Master>\n        <section class=\"py-5\" style=\"min-height: 472px\">\n            <div class=\"container\" v-if=\"!product\">\n                <h1>There is no products named by <span>\"{{ this.$route.params.slug }}\"</span></h1>\n            </div>\n            <div class=\"container\" v-else>\n                \n                <div class=\"row\">\n                    <div class=\"col-sm-12 col-lg-6\">\n                        <div\n                            class=\"w-100 d-flex justify-content-center\"\n                            style=\"height: 530px\"\n                        >\n                            <img :src=\"product.images\" alt=\"\" style=\"height:100%;\">\n                        </div>\n                    </div>\n                    <div class=\"col-sm-12 col-lg-6\">\n                        <div class=\"pl-3\">\n                            <h4 class=\"\" v-text=\"product.name\"></h4>\n                            <div class=\"my-3\">\n                                <span\n                                    class=\"text-black-50\"\n                                    v-text=\"product.category.name\"\n                                ></span>\n                                <span class=\"text-black-50 fw-bold mx-2\">\n                                    .\n                                </span>\n                                <span class=\"text-black-50\">\n                                    <i class=\"fa-solid fa-shopping-basket\"></i>\n                                    {{ product.order_count === 0 ? 0 : product.order_count }} {{ product.order_count > 1 ? 'orders' : 'order' }}\n                                </span>\n                                <span class=\"text-black-50 fw-bold mx-2\">\n                                    .\n                                </span>\n                                <span\n                                    :class=\"\n                                        product.inventory === 0\n                                                ? 'text-black-50 text-decoration-line-through'\n                                                : 'text-success'\n                                    \"\n                                    v-text=\"\n                                        product.inventory === 0\n                                            ? 'Out of stock'\n                                            : 'In stock'\n                                    \"\n                                ></span>\n                            </div>\n                            <h3\n                                class=\"mb-3\"\n                                v-text=\"formatCurrency(product.price)\"\n                            ></h3>\n                            <p\n                                class=\"text-black-50\"\n                                v-text=\"product.description\"\n                            ></p>\n                            <hr />\n                            <div class=\"row mb-4\">\n                                <div class=\"col-md-4 col-6\">\n                                    <label class=\"form-label\">Size</label>\n                                    <select class=\"form-select\" v-model=\"size\">\n                                        <option value=\"small\">Small</option>\n                                        <option value=\"medium\">Medium</option>\n                                        <option value=\"large\">Large</option>\n                                    </select>\n                                </div>\n                                <div class=\"col-md-3 col-6\">\n                                    <label class=\"form-label\">Quantity</label>\n                                    <vue-number-input\n                                        v-if=\"product.inventory === 0\"\n                                        id=\"quantity\"\n                                        :min=\"0\"\n                                        v-model.number=\"outofstock\"\n                                        :max=\"0\"\n                                        inline\n                                        center\n                                        controls\n                                    ></vue-number-input>\n                                    <vue-number-input\n                                        v-else\n                                        id=\"quantity\"\n                                        :min=\"1\"\n                                        v-model.number=\"quantity\"\n                                        :max=\"product.inventory\"\n                                        inline\n                                        center\n                                        controls\n                                    ></vue-number-input>\n                                </div>\n                            </div>\n                            <button\n                                @click.prevent=\"\n                                    this.$store.state.EmailVerification\n                                        ? this.$store.dispatch('addToCart', {\n                                              product,\n                                              quantity,\n                                              size,\n                                          })\n                                        : $router.push('/login')\n                                \"\n                                class=\"btn btn-secondary me-2\"\n                                :class=\"\n                                    product.inventory === 0\n                                            ? 'disabled text-decoration-line-through'\n                                            : ''\n                                \"\n                            >\n                                <i class=\"fa-solid fa-shopping-basket\"></i>\n                                {{\n                                    product.inventory === 0\n                                            ? \"Out Of Stock\"\n                                            : \"Add To Cart\"\n                                    \n                                }}\n                            </button>\n                \n                            <button\n                                @click=\"\n                                    this.$store.state\n                                        .EmailVerification\n                                        ? this.$store.dispatch(\n                                                'likeProduct',\n                                                {\n                                                    product,\n                                                    current,\n                                                }\n                                            )\n                                        : $router.push('/login')\n                                \"\n                                class=\"btn btn-light\"\n                            >\n                                <i\n                                    :style=\"\n                                        product.is_like\n                                            ? 'color:#E73862'\n                                            : ''\n                                    \"\n                                    class=\"fa fa-heart\"\n                                ></i>\n\n                                &nbsp;\n                                <span class=\"text\">{{\n                                    product.like_count\n                                }}</span>\n                            </button>\n                            <p\n                                class=\"mt-3 fw-bold\"\n                            >{{ isInCart? isInCart.quantity : 0 }} in cart</p>\n                        </div>\n                    </div>\n                </div>\n\n                <h5 class=\"text fw-bold comment-count text-start mt-5\">\n                    {{ product.comment_count }} {{ product.comment_count > 1 ? \"Comments\" : \"Comment\" }}\n                </h5>\n                <div class=\"d-flex mt-4\" v-if=\"this.$store.state.auth\">\n                    <img\n                        class=\"profile me-2\"\n                        :src=\"\n                            this.$store.state.auth\n                                ? this.$store.state.auth.user.profile\n                                : '/images/default.png'\n                        \"\n                        alt=\"Profile\"\n                    />\n                    <form\n                        class=\"comment-form mt-2\"\n                        @submit.prevent=\"\n                            this.$store.state.EmailVerification\n                                ? this.$store\n                                      .dispatch('addComment', {\n                                          body,\n                                          product,\n                                      })\n                                      .then((_) => (body = ''))\n                                : $router.push('/login')\n                        \"\n                    >\n                        <div class=\"input-group\">\n                            <input\n                                type=\"text\"\n                                v-model=\"body\"\n                                class=\"form-control input-form\"\n                                style=\"border-right: none\"\n                                placeholder=\"Add Comment\"\n                            />\n                            <button\n                                type=\"submit\"\n                                class=\"input-group-text\"\n                                style=\"\n                                    background-color: white;\n                                    border-left: none;\n                                \"\n                            >\n                                <i class=\"fa-solid fa-paper-plane\"></i>\n                            </button>\n                        </div>\n                    </form>\n                </div>\n                <div class=\"row mt-5\">\n                    <Comments\n                        :comment=\"comment\"\n                        v-for=\"comment in product.comments\"\n                        :key=\"comment.id\"\n                        :product=\"product\"\n                    />\n                </div>\n            </div>\n        </section>\n    </Master>\n</template>\n<script>\nimport VueNumberInput from \"@chenfengyuan/vue-number-input\";\nimport Comments from \"./Comments.vue\";\nimport Master from \"../layouts/Master\";\nexport default {\n    data() {\n        return {\n            quantity: 1,\n            outofstock : 0,\n            size: \"small\",\n            body: \"\",\n        };\n    },\n    created(){\n        this.$Progress.start();\n        if(this.isInCart){\n            this.quantity = this.isInCart.quantity;\n        }\n    },\n    mounted(){\n        this.$Progress.finish();\n    },\n    components: { Master, VueNumberInput, Comments },\n    computed: {\n        product() {\n            if (this.$route.params.slug) {\n                return this.$store.state.products.find(\n                    (product) => product.slug === this.$route.params.slug\n                );\n            }\n        },\n        isInCart() {\n            const slug = this.$route.params.slug;\n            const carts = this.$store.state.cart;\n            const isInCart = carts.find((cart) => cart.product.slug == slug);\n            return isInCart;\n        },\n        \n    },\n    methods: {\n        formatCurrency(price) {\n            price = price / 100;\n            return price.toLocaleString(\"en-US\", {\n                style: \"currency\",\n                currency: \"USD\",\n            });\n        },\n    },\n};\n</script>\n\n<style scoped>\n.comment-form {\n    min-width: 350px;\n    width: 90%;\n}\n.profile {\n    width: 50px;\n    border-radius: 50%;\n}\n\n.input-group-text {\n    color: #e73862;\n    font-size: 1.3rem;\n}\n</style>\n"],"sourceRoot":""}]);
